import { Builder } from '../../Builder'

export interface InputLocationMessageContentPayload {
    latitude: number
    longitude: number
    horizontal_accuracy?: number
    live_period?: number
    heading?: number
    proximity_alert_radius?: number
}

export class InputLocationMessageContent extends Builder<InputLocationMessageContentPayload> {

    public constructor() {
        super({ latitude: 0, longitude: 0 })
    }

    public latitude(value: number): this {
        this.packet.latitude = value
        return this
    }

    public longitude(value: number): this {
        this.packet.longitude = value
        return this
    }

    public horizontalAccuracy(accuracy?: number): this {
        this.packet.horizontal_accuracy = accuracy
        return this
    }

    public livePeriod(period?: number): this {
        this.packet.live_period = period
        return this
    }

    public heading(value?: number): this {
        this.packet.heading = value
        return this
    }

    public proximityAlertRadius(radius?: number): this {
        this.packet.proximity_alert_radius = radius
        return this
    }
}
